import {
    Component,
    h,
    Host,
    Prop,
    Element,
    Event,
    EventEmitter,
    Watch,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

const layoutVariantsConfig = {
    variants: {
        layout: {
            column: 'flex flex-col gap-4',
            row: 'flex',
            grid: 'grid grid-cols-3',
        },
    },
};

export interface CheckboxOptions {
    option_id: string;
    label?: string;
    hint_text?: string;
    value?: boolean;
}

export type CheckboxLayout = 'column' | 'row' | 'grid';

/**
 * ### ```interface CheckboxOptions```
 * ```
 *  id: string;
 *  label?: string;
 *  hint_text?: string;
 *  value?: boolean;
 * ```
 *
 * @category Form Controls
 * @description A checkbox group component for selecting multiple options from a predefined list. Provides grouped validation, labeling, and accessibility features for checkbox collections.
 * @slot - Used to place checkboxes manually when 'options' property is not set.
 */
@Component({
    tag: 'at-checkbox-group',
    shadow: false,
})
export class AtCheckboxGroup {
    /**
     * Label of the checkbox group.
     */
    @Prop()
    label?: string;

    /**
     * Info icon with detailed tooltip description. Displayed at right of label.
     */
    @Prop()
    info_text?: string;

    /**
     * Hint to be placed below the label
     */
    @Prop()
    hint_text?: string;

    /**
     * Displayed when the input is invalid
     */
    @Prop()
    error_text?: string;

    /**
     * Disables interaction with the checkbox
     */
    @Prop()
    disabled?: boolean;

    /**
     * Shows the error text
     */
    @Prop()
    invalid?: boolean;

    /**
     * Adds a required * to the label
     */
    @Prop()
    required?: boolean;

    /**
     * Options displayed in the checkbox group.
     */
    @Prop()
    options: CheckboxOptions[];

    @Watch('options')
    updateSelected() {
        this.options.forEach((option) => {
            if (
                option.value === true &&
                !this.value.includes(option.option_id)
            ) {
                this.value.push(option.option_id);
            } else if (
                option.value === false &&
                this.value.includes(option.option_id)
            ) {
                this.value = this.value.filter(
                    (selection) => selection !== option.option_id,
                );
            }
        });
    }

    /**
     * Sets the layout of the checkbox-group
     */
    @Prop()
    layout?: CheckboxLayout = 'column';

    /**
     * A list of strings, containing the Id's of the currently selected checkboxes
     */
    @Prop({ mutable: true })
    value: string[] = [];

    @Element() el: HTMLAtCheckboxGroupElement;

    checkboxEls: HTMLAtCheckboxElement[] = [];

    /**
     * Emits an event containing the 'selected' prop when changed
     */
    @Event()
    atuiChange: EventEmitter<string[]>;

    private formControlSelected: string[] = null;

    componentDidLoad() {
        if (!this.getCheckBoxes) {
            this.el
                .querySelectorAll('at-checkbox')
                .forEach((checkboxEl, index) => {
                    checkboxEl.option_id = `${index}`;
                    checkboxEl.disabled = this.disabled;
                    checkboxEl.value = this.value.includes(`${index}`);
                    checkboxEl.addEventListener('atuiChange', (event) =>
                        this.handleChange(event, `${index}`),
                    );
                    this.checkboxEls.push(checkboxEl);
                });
        }
    }

    get getCheckBoxes(): any {
        if (this.options) {
            if (this.formControlSelected !== null) {
                this.value = this.formControlSelected;
                this.formControlSelected = null;
            }
            return this.options.map((option) => (
                <at-checkbox
                    option_id={option.option_id}
                    label={option.label}
                    hint_text={option.hint_text}
                    value={this.value.includes(option.option_id)}
                    disabled={this.disabled}
                    onAtuiChange={(event) =>
                        this.handleChange(event, option.option_id)
                    }
                />
            ));
        }
        return '';
    }

    handleChange(event: CustomEvent, checkboxId: string) {
        event.stopPropagation();
        this.value = this.value.includes(checkboxId)
            ? this.value.filter((option) => option !== checkboxId)
            : [...this.value, checkboxId];
        this.atuiChange.emit(this.value);
    }

    render() {
        const getLayoutClassname = classlist('gap-4', layoutVariantsConfig);
        const classname = getLayoutClassname({
            layout: this.layout,
        });
        return (
            <Host
                role="group"
                aria-label={this.label}
                aria-description={this.info_text}
                class="flex w-full flex-col"
            >
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <at-form-label
                            label={this.label}
                            required={this.required}
                            info_text={this.info_text}
                        />
                    )}
                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="checkbox-group-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <ul
                    class={classname}
                    data-name="checkbox-group-options"
                >
                    <slot />
                    {this.getCheckBoxes &&
                        this.getCheckBoxes.map((checkbox) => (
                            <li class="flex">{checkbox}</li>
                        ))}
                </ul>

                {this.error_text && this.invalid && (
                    <span
                        class="text-error text-sm"
                        data-name="checkbox-group-error-text"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}

/**
 * @category Form Controls
 * @description A checkbox group component for selecting multiple options from a predefined list. Provides grouped validation, labeling, and accessibility features for checkbox collections.
 *
 * @slot - Used to place checkboxes manually when 'options' property is not set.
 */
